@import url("https://use.fontawesome.com/releases/v5.15.1/css/all.css");body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}@keyframes App-logo-spin{from{transform:skew(0deg)}to{transform:skew(30deg, 20deg)}}.App-main{margin:3rem 0}.App-main h2,.App-main p{font-size:calc(10px + 2vmin);color:#282c34;line-height:1.5em;font-weight:600;letter-spacing:.03em}.App-main .App-logo{height:40vmin;pointer-events:none;margin:3rem 0}.App-main .App-link{color:#282c34;margin:.8em}@media (prefers-reduced-motion: no-preference){.App-logo{animation:App-logo-spin infinite 20s linear}}
