@import url("https://use.fontawesome.com/releases/v5.15.1/css/all.css");body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}@keyframes App-logo-spin{from{transform:skew(0deg)}to{transform:skew(30deg, 20deg)}}.App-header{background-color:white;min-height:15vh;display:flex;flex-direction:row;align-items:center;justify-content:space-between;font-size:calc(10px + 2vmin);color:#282c34}.App-header h1{color:#282c34;line-height:1.5em;font-weight:500;letter-spacing:.03em}@media all and (max-width: 1200px){.App-header{flex-direction:row;justify-content:space-between}}@media all and (max-width: 900px){.App-header{flex-direction:column;justify-content:center}}@media all and (max-width: 600px){.App-header{flex-direction:column;justify-content:center}}
