@import url("https://use.fontawesome.com/releases/v5.15.1/css/all.css");body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}@keyframes App-logo-spin{from{transform:skew(0deg)}to{transform:skew(30deg, 20deg)}}.App-header--nav{display:flex;align-items:center;justify-content:center}.App-header--nav ul{display:flex;list-style:none;padding-inline-start:0}.App-header--nav ul li a{padding:2rem;text-decoration:none;color:#282c34}@media all and (min-width: 1200px){.App-header--ul{align-items:center}}@media all and (max-width: 599px){.App-header--ul{flex-direction:column;align-items:center;justify-content:stretch}.App-header--ul li{margin:.5rem 0}}
